nded=\"false\" data-bs-target=\"#dropdown-menu\"/>\n              <i\n                class=\"mdi mdi-chevron-down chevronIcon\"\n                data-bs-toggle=\"dropdown\"\n                aria-expanded=\"false\"\n                data-bs-target=\"#dropdown-menu\"\n                id=\"dropdownMenuButton1\"\n                style=\"padding-top: 10px; margin-right: -11px; font-size: 16px; color: #FFFFFF;\"\n              ></i>\n              <ul style=\"width: 246px\" class=\"dropdown-menu user-info-dropwdown\" aria-labelledby=\"dropdownMenuButton1\">\n                <li>\n                  <div class=\"user-info\">\n                    <img src=\"../../../../images/profile-image-dark-in-menu.svg\" style=\"margin-right: 12px;\"/>\n                    <div style=\"display: grid;\">\n                      <span class=\"user-name\">{{ user.name + ' ' + user.lastName }}</span>\n                      <span class=\"user-email\">{{ user.email }}</span>\n                    </div>\n\n                  </div>\n                </li>\n                <li>\n                  <div class=\"menuıtem\" @click=\"logout()\">\n                    <i class=\"mdi mdi-logout\" style=\"font-size: 20px; margin-right: 8px;\"></i>\n                    <a>Logout</a>\n                  </div>\n                </li>\n              </ul>\n            </div>\n          </div>\n        </div>\n      </div>\n    </nav>\n    <div v-if=\"breadcrumb\" class=\"breadcrumb-div\">\n        <div class=\"back-button-div\" @click=\"gotoBack()\">\n            <i class=\"mdi mdi-arrow-left-box back-button\"></i>\n        </div>\n        <hr style=\"width: 1px; height: 36px; color: #D5D5D5; margin: 0; opacity: 1\">\n        <div v-for=\"item in breadcrumb\" :key=\"item\" :style=\"{color: breadcrumb.indexOf(item) < breadcrumb.length - 1 ? '#A1A1A1' : ''}\" class=\"breadcrumb-items\" >{{ item }}<span v-if=\"breadcrumb.indexOf(item) < breadcrumb.length - 1\" style=\"margin-left: 12px;\">/</span></div>\n        \n